* {
    font-family: 'Times New Roman', Times, serif;
}
body {
    padding: 60px;
}
input{
    width: 100%;
    font-size: 1.2rem;
}
.card{
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    margin-bottom: 6px;
    margin-right: 10px;
    overflow: hidden;
}

.card p{
    padding: 5px;
    text-align: center;
}
.card img{
    margin:0px auto;
    max-height: 300px;
    width: 100%;
}